"use client";

import React, { useState } from "react";
import TweetInput from "../components/twitter/TweetInput";

const initialTweets = [
  {
    id: 1,
    user: {
      name: "John Doe",
      username: "johndoe",
      avatar: "https://randomuser.me/api/portraits/men/32.jpg",
    },
    content: "This is a sample tweet for the Twitter-like UI.",
    timestamp: "2h",
  },
  {
    id: 2,
    user: {
      name: "Jane Smith",
      username: "janesmith",
      avatar: "https://randomuser.me/api/portraits/women/44.jpg",
    },
    content: "Another example tweet with minimalistic design.",
    timestamp: "3h",
  },
];

const Sidebar = () => {
  return (
    <nav className="sticky top-0 h-screen p-4 w-64 border-r border-gray-300 bg-white dark:bg-black dark:border-gray-700 flex flex-col">
      <h1 className="text-2xl font-bold mb-8 text-black dark:text-white">MiniTwitter</h1>
      <ul className="space-y-6 text-gray-700 dark:text-gray-300">
        <li className="hover:text-blue-500 cursor-pointer font-semibold">Home</li>
        <li className="hover:text-blue-500 cursor-pointer font-semibold">Explore</li>
        <li className="hover:text-blue-500 cursor-pointer font-semibold">Notifications</li>
        <li className="hover:text-blue-500 cursor-pointer font-semibold">Messages</li>
        <li className="hover:text-blue-500 cursor-pointer font-semibold">Bookmarks</li>
        <li className="hover:text-blue-500 cursor-pointer font-semibold">Profile</li>
        <li className="hover:text-blue-500 cursor-pointer font-semibold">More</li>
      </ul>
    </nav>
  );
};

const Tweet = ({ tweet }: { tweet: typeof initialTweets[0] }) => {
  return (
    <div className="border-b border-gray-300 dark:border-gray-700 p-4 flex space-x-4 hover:bg-gray-100 dark:hover:bg-gray-900 transition-colors">
      <img
        src={tweet.user.avatar}
        alt={tweet.user.name}
        className="w-12 h-12 rounded-full"
      />
      <div className="flex-1">
        <div className="flex items-center space-x-2">
          <span className="font-bold text-black dark:text-white">{tweet.user.name}</span>
          <span className="text-gray-500">@{tweet.user.username}</span>
          <span className="text-gray-500">Â· {tweet.timestamp}</span>
        </div>
        <p className="text-black dark:text-white mt-1">{tweet.content}</p>
      </div>
    </div>
  );
};

const Feed = () => {
  const [tweets, setTweets] = useState(initialTweets);
  const [newTweet, setNewTweet] = useState("");

  const handleTweetSubmit = () => {
    if (newTweet.trim() === "") return;
    const newTweetObj = {
      id: tweets.length + 1,
      user: {
        name: "Current User",
        username: "currentuser",
        avatar: "https://randomuser.me/api/portraits/lego/1.jpg",
      },
      content: newTweet,
      timestamp: "now",
    };
    setTweets([newTweetObj, ...tweets]);
    setNewTweet("");
  };

  return (
    <main className="flex-1 max-w-xl border-x border-gray-300 dark:border-gray-700 bg-white dark:bg-black mx-auto">
      <header className="sticky top-0 bg-white dark:bg-black border-b border-gray-300 dark:border-gray-700 p-4 text-xl font-bold text-black dark:text-white">
        Home
      </header>
      <TweetInput newTweet={newTweet} setNewTweet={setNewTweet} onTweetSubmit={handleTweetSubmit} />
      <div>
        {tweets.map((tweet) => (
          <Tweet key={tweet.id} tweet={tweet} />
        ))}
      </div>
    </main>
  );
};

const RightSidebar = () => {
  return (
    <aside className="sticky top-0 h-screen p-4 w-80 border-l border-gray-300 bg-white dark:bg-black dark:border-gray-700 hidden lg:flex flex-col">
      <div className="mb-6">
        <input
          type="text"
          placeholder="Search MiniTwitter"
          className="w-full p-2 rounded-full border border-gray-300 dark:border-gray-700 bg-gray-100 dark:bg-gray-900 text-black dark:text-white focus:outline-none focus:ring-2 focus:ring-blue-500"
        />
      </div>
      <div>
        <h2 className="text-lg font-bold mb-4 text-black dark:text-white">Trends for you</h2>
        <ul className="space-y-3 text-gray-700 dark:text-gray-300">
          <li className="hover:bg-gray-100 dark:hover:bg-gray-900 p-2 rounded cursor-pointer">
            #Minimalism
          </li>
          <li className="hover:bg-gray-100 dark:hover:bg-gray-900 p-2 rounded cursor-pointer">
            #NextJS
          </li>
          <li className="hover:bg-gray-100 dark:hover:bg-gray-900 p-2 rounded cursor-pointer">
            #TailwindCSS
          </li>
          <li className="hover:bg-gray-100 dark:hover:bg-gray-900 p-2 rounded cursor-pointer">
            #React
          </li>
        </ul>
      </div>
    </aside>
  );
};

export default function Page() {
  return (
    <div className="flex min-h-screen bg-gray-50 dark:bg-gray-900 text-black dark:text-white font-sans justify-center">
      <Sidebar />
      <Feed />
      <RightSidebar />
    </div>
  );
}
