"use client";

import React from "react";

interface TweetInputProps {
  newTweet: string;
  setNewTweet: React.Dispatch<React.SetStateAction<string>>;
  onTweetSubmit: () => void;
}

export default function TweetInput({ newTweet, setNewTweet, onTweetSubmit }: TweetInputProps) {
  return (
    <div className="p-4 border-b border-gray-300 dark:border-gray-700">
      <textarea
        className="w-full p-2 border border-gray-300 dark:border-gray-700 rounded resize-none bg-gray-50 dark:bg-gray-900 text-black dark:text-white focus:outline-none focus:ring-2 focus:ring-blue-500"
        rows={3}
        placeholder="What's happening?"
        value={newTweet}
        onChange={(e) => setNewTweet(e.target.value)}
      />
      <div className="flex justify-end mt-2">
        <button
          onClick={onTweetSubmit}
          className="bg-blue-600 hover:bg-blue-700 text-white font-semibold py-1 px-4 rounded disabled:opacity-50"
          disabled={newTweet.trim() === ""}
        >
          Tweet
        </button>
      </div>
    </div>
  );
}
